@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap');

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body{
  background-color: beige;
}

section{
  width: 80vw;
  max-width: 400px;
  margin: 5rem auto;
  display: grid;
  place-items: center;
  background: #d13ca4dc;
  border-radius: 1%;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
  font-family: 'Montserrat', sans-serif;

}
section h1{
  font-size: 1rem;
  color: white;
  text-align: center;
  margin: 1rem auto;
}

article{
  width: 70%;
  height: 79px;
  max-width: 300px;
  color: rgba(41, 35, 35, 0.8);
  margin: 1rem auto;
  display: flex;
  align-items: center;
  border-radius: 3%;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
  background: #ffea04d8;

  
  /* padding: 0.3rem 0; */
}

img{
  display: block;
  border-radius: 50%;
  height: 75px;
  margin:0 1rem;
}

.info{
  /* text-align: left; */
  padding: 2rem 0;
  text-transform: capitalize;
  margin-left: 0.5rem;
}

article:hover{
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.9);
}

.btn{
  width: 60%;
  margin: 1.5rem auto;
  color:  rgba(255, 255, 255, 0.918);
  border: none;
  cursor: pointer;
  font-size: 1.2rem;
  text-transform: capitalize;
  letter-spacing: 0.2rem;
   border-radius: 3%;
   padding: 0.4rem 0.8rem;
   background: #fe3a9e; 
   background-color: rgb(14, 13, 13);
}

.btn:hover{
  background: #0d1137;
}